import { printMessage } from './info-messages';
export const moveDispatcher = (ajaxurl) => {
    const moveFromRadios = document.querySelectorAll('input[name="move-from"]');
    const moveToRadios = document.querySelectorAll('input[name="move-to"]');
    const dispatcherSections = document.querySelectorAll('.dispatcher-section');
    const dispatcherCheckboxes = document.querySelectorAll('input[name="dispatcher[]"]');
    const weekendExclusions = document.querySelectorAll('.weekend-exclusions');
    function updateDispatcherOptions() {
        const selectedRadio = document.querySelector('input[name="move-from"]:checked');
        if (selectedRadio) {
            const teamData = JSON.parse(selectedRadio.getAttribute('data-team') || '[]');
            const teamArray = Array.isArray(teamData) ? teamData : Object.values(teamData || {});
            dispatcherSections.forEach((section) => {
                section.style.display = 'none';
            });
            dispatcherSections.forEach((section) => {
                const dispatcherOption = section.querySelector('.dispatcher-option');
                if (!dispatcherOption)
                    return;
                const userId = dispatcherOption.getAttribute('data-user-id') || '';
                const userIdNumber = userId.replace('user_', '');
                console.log('userIdNumber', userIdNumber);
                if (teamArray.includes(parseInt(userIdNumber, 10))) {
                    section.style.display = 'block';
                }
            });
            document.querySelectorAll('input[name="dispatcher[]"]').forEach((checkbox) => {
                checkbox.checked = false;
            });
            hideAllWeekendExclusions();
            const moveFromUserId = selectedRadio.value;
            const formData = new FormData();
            formData.append('action', 'get_dispatchers_weekends');
            formData.append('user_id', moveFromUserId);
            fetch(ajaxurl, {
                method: 'POST',
                body: formData,
            })
                .then((response) => response.json())
                .then((data) => {
                if (data.success) {
                    const weekends = data.data;
                    document.querySelectorAll('.exclude-checkbox').forEach((cb) => {
                        cb.checked = false;
                    });
                    Object.entries(weekends).forEach(([day, ids]) => {
                        ids.forEach((id) => {
                            const cb = document.querySelector(`.exclude-checkbox[data-user-id="user_${id}"][data-day="${day}"]`);
                            if (cb)
                                cb.checked = true;
                        });
                    });
                }
            });
        }
        else {
            dispatcherSections.forEach((section) => {
                section.style.display = 'none';
            });
            hideAllWeekendExclusions();
        }
        updateMoveToRadios();
    }
    function updateMoveToRadios() {
        const selectedMoveFrom = document.querySelector('input[name="move-from"]:checked');
        const selectedId = selectedMoveFrom ? selectedMoveFrom.value : null;
        const selectedDispatchers = Array.from(document.querySelectorAll('input[name="dispatcher[]"]:checked')).map((cb) => cb.value);
        moveToRadios.forEach((radio) => {
            if (radio.value === selectedId) {
                radio.disabled = true;
                if (radio.checked)
                    radio.checked = false;
            }
            else {
                radio.disabled = false;
            }
        });
    }
    function showWeekendExclusions(selectedDispatcherCheckbox) {
        const dispatcherSection = selectedDispatcherCheckbox.closest('.dispatcher-section');
        if (!dispatcherSection)
            return;
        const exclusionsDiv = dispatcherSection.querySelector('.weekend-exclusions');
        console.log('exclusionsDiv', exclusionsDiv);
        if (exclusionsDiv) {
            exclusionsDiv.style.display = 'block';
        }
    }
    function hideWeekendExclusions(selectedDispatcherCheckbox) {
        const dispatcherSection = selectedDispatcherCheckbox.closest('.dispatcher-section');
        if (!dispatcherSection)
            return;
        const exclusionsDiv = dispatcherSection.querySelector('.weekend-exclusions');
        if (exclusionsDiv) {
            exclusionsDiv.style.display = 'none';
        }
    }
    function hideAllWeekendExclusions() {
        weekendExclusions.forEach((exclusion) => {
            exclusion.style.display = 'none';
        });
    }
    moveFromRadios.forEach((radio) => {
        radio.addEventListener('change', () => {
            updateDispatcherOptions();
        });
    });
    dispatcherCheckboxes.forEach((checkbox) => {
        checkbox.addEventListener('change', function () {
            if (this.checked) {
                showWeekendExclusions(this);
            }
            else {
                hideWeekendExclusions(this);
            }
            updateMoveToRadios();
        });
    });
    const saveAllWeekendsBtn = document.getElementById('save-all-weekends-btn');
    if (saveAllWeekendsBtn) {
        saveAllWeekendsBtn.addEventListener('click', function (event) {
            const target = event.target;
            target.setAttribute('disabled', 'disabled');
            const form = target.closest('form');
            if (!form)
                return;
            const formData = new FormData(form);
            formData.append('action', 'debug_save_weekends');
            fetch(ajaxurl, {
                method: 'POST',
                body: formData,
            })
                .then((res) => res.json())
                .then((requestStatus) => {
                if (requestStatus.success) {
                    printMessage(requestStatus.data.message, 'success', 8000);
                    setTimeout(() => {
                        window.location.reload();
                    }, 2000);
                }
                else {
                    if (target) {
                        target.removeAttribute('disabled');
                    }
                    printMessage(requestStatus.data.message, 'danger', 8000);
                }
            })
                .catch((error) => {
                printMessage(`Request failed: ${error}`, 'danger', 8000);
                console.error('Request failed:', error);
            });
        });
    }
};
