export const setStatusPaid = () => {
    const selectElementAll = document.querySelectorAll('.js-select-status-factoring');
    selectElementAll.forEach((item) => {
        item.addEventListener('change', (event) => {
            const target = event.target;
            const selectedValue = target.value;
            const previousValue = target.dataset.previousValue || '';
            if (selectedValue === 'paid') {
                const confirmationMessage = "If you select 'Paid', make sure editing is finished, as the load will be locked and no further changes will be possible.";
                const userConfirmed = confirm(confirmationMessage);
                if (userConfirmed) {
                    target.dataset.previousValue = selectedValue;
                }
                else {
                    target.value = previousValue;
                }
            }
            else {
                target.dataset.previousValue = selectedValue;
            }
        });
    });
};
