export function generateCustomerTemplate({ id, name, address, mc, dot, contact, phone, email }) {
    const dotTmpl = dot ? `<span><strong>#DOT:</strong> ${dot}</span>` : '';
    const mcTmpl = mc ? `<span><strong>#MC:</strong> ${mc}</span>` : '';
    return `
        <ul class="result-search-el">
            <li class="name">${name !== null && name !== void 0 ? name : ''}</li>
            <li class="address">${address !== null && address !== void 0 ? address : ''}</li>
            <li>${mcTmpl}</li>
            <li>${dotTmpl}</li>
            <li>${contact !== null && contact !== void 0 ? contact : ''}</li>
            <li>${phone !== null && phone !== void 0 ? phone : ''}</li>
            <li>${email !== null && email !== void 0 ? email : ''}</li>
        </ul>
        <input type="hidden" name="customer_id" value="${id !== null && id !== void 0 ? id : ''}">
    `;
}
