import { initGoogleCharts, parseChartDataFromAttribute, drawPieChart, drawBarChart, convertToChartDataArray } from './google-charts-utils';
export function initChart(chartId, useBarChart = false) {
    const chartElement = document.getElementById(chartId);
    if (!chartElement) {
        return;
    }
    if (chartElement.dataset.initialized === 'true') {
        return;
    }
    const chartData = parseChartDataFromAttribute(chartElement);
    if (!chartData || chartData.length === 0) {
        return;
    }
    const getContainerSize = () => {
        const rect = chartElement.getBoundingClientRect();
        return {
            width: rect.width || parseInt(getComputedStyle(chartElement).width, 10) || 800,
            height: parseInt(getComputedStyle(chartElement).height, 10) || 600,
        };
    };
    initGoogleCharts(() => {
        const containerSize = getContainerSize();
        const dataArray = convertToChartDataArray(chartData, ['Type', 'Count']);
        if (useBarChart) {
            const dataCount = dataArray.length - 1;
            const minBarHeight = 35;
            const calculatedHeight = Math.max(containerSize.height, dataCount * minBarHeight + 100);
            const dataWithAnnotations = dataArray.map((row, index) => {
                if (index === 0) {
                    return [...row, 'Annotation'];
                }
                return [...row, String(row[1])];
            });
            drawBarChart(chartElement, dataWithAnnotations, {
                width: containerSize.width,
                height: calculatedHeight,
                chartArea: {
                    left: '15%',
                    top: '0%',
                    width: '82%',
                    height: '98%',
                    right: '3%',
                    bottom: '2%',
                },
                hAxis: {
                    textStyle: {
                        fontSize: 13,
                    },
                    baseline: 0,
                    gridlines: {
                        count: 5,
                    },
                    viewWindow: {
                        min: 0,
                    },
                },
                vAxis: {
                    textStyle: {
                        fontSize: 13,
                    },
                },
                bars: {
                    groupWidth: '90%',
                },
                fontSize: 13,
                colors: ['#4285f4'],
                annotations: {
                    textStyle: {
                        fontSize: 12,
                        bold: true,
                    },
                    alwaysOutside: false,
                },
            });
        }
        else {
            drawPieChart(chartElement, dataArray, {
                width: containerSize.width,
                height: containerSize.height,
                pieSliceText: 'value',
                pieSliceTextStyle: {
                    fontSize: 14,
                    bold: true,
                },
                legend: {
                    position: 'right',
                    textStyle: {
                        fontSize: 12,
                    },
                },
                chartArea: {
                    width: '75%',
                    height: '85%',
                    left: '5%',
                    top: '5%',
                },
                fontSize: 14,
                tooltip: {
                    textStyle: {
                        fontSize: 12,
                    },
                },
            });
        }
        chartElement.dataset.initialized = 'true';
    });
}
export function initDriversStatisticsCharts() {
    const chartConfigs = [
        { id: 'stateChart', useBar: true, container: 'home-location' },
        { id: 'vehicleTypeChart', useBar: false, container: 'vehicle-type' },
        { id: 'nationalityChart', useBar: true, container: 'nationality' },
        { id: 'languageChart', useBar: false, container: 'languages' },
        { id: 'loadsByStateChart', useBar: true, container: 'loads-by-state' },
    ];
    chartConfigs.forEach((config) => {
        const chartElement = document.getElementById(config.id);
        if (chartElement) {
            const container = chartElement.closest('.chart-container');
            if (container) {
                const isVisible = (container.style.display !== 'none' &&
                    window.getComputedStyle(container).display !== 'none') ||
                    !container.hasAttribute('style');
                if (isVisible) {
                    chartElement.dataset.initialized = 'false';
                    initChart(config.id, config.useBar);
                }
            }
            else {
                chartElement.dataset.initialized = 'false';
                initChart(config.id, config.useBar);
            }
        }
    });
    const tabElements = document.querySelectorAll('#driversStatisticsTabs button[data-tab-name]');
    tabElements.forEach((tabElement) => {
        tabElement.addEventListener('click', (event) => {
            const tabName = event.target.getAttribute('data-tab-name');
            setTimeout(() => {
                if (tabName === 'loads-by-state') {
                    const loadsByStateChart = document.getElementById('loadsByStateChart');
                    if (loadsByStateChart) {
                        loadsByStateChart.dataset.initialized = 'false';
                        if (typeof window.initLoadsByStateChart === 'function') {
                            window.initLoadsByStateChart();
                        }
                    }
                }
                else if (tabName === 'loads-by-route') {
                    const loadsByRouteChart = document.getElementById('loadsByRouteChart');
                    const loadsByRouteChart1 = document.getElementById('loadsByRouteChart1');
                    const loadsByRouteChart2 = document.getElementById('loadsByRouteChart2');
                    if (loadsByRouteChart) {
                        loadsByRouteChart.dataset.initialized = 'false';
                        if (typeof window.initLoadsByRouteChart === 'function') {
                            window.initLoadsByRouteChart('loadsByRouteChart');
                        }
                    }
                    if (loadsByRouteChart1) {
                        loadsByRouteChart1.dataset.initialized = 'false';
                        if (typeof window.initLoadsByRouteChart === 'function') {
                            window.initLoadsByRouteChart('loadsByRouteChart1');
                        }
                    }
                    if (loadsByRouteChart2) {
                        loadsByRouteChart2.dataset.initialized = 'false';
                        if (typeof window.initLoadsByRouteChart === 'function') {
                            window.initLoadsByRouteChart('loadsByRouteChart2');
                        }
                    }
                }
                else {
                    const chartConfigs = [
                        { id: 'stateChart', useBar: true },
                        { id: 'vehicleTypeChart', useBar: false },
                        { id: 'nationalityChart', useBar: true },
                        { id: 'languageChart', useBar: false },
                    ];
                    chartConfigs.forEach((config) => {
                        const chartElement = document.getElementById(config.id);
                        if (chartElement) {
                            chartElement.dataset.initialized = 'false';
                            initChart(config.id, config.useBar);
                        }
                    });
                }
            }, 100);
        });
    });
    const bootstrapTabElements = document.querySelectorAll('#driversStatisticsTabs button[data-bs-toggle="tab"]');
    bootstrapTabElements.forEach((tabElement) => {
        tabElement.addEventListener('shown.bs.tab', (event) => {
            const targetId = event.target.getAttribute('data-bs-target');
            if (targetId) {
                const tabPane = document.querySelector(targetId);
                if (tabPane) {
                    const chartConfigs = [
                        { id: 'stateChart', useBar: true },
                        { id: 'vehicleTypeChart', useBar: false },
                        { id: 'nationalityChart', useBar: true },
                        { id: 'languageChart', useBar: false },
                        { id: 'loadsByStateChart', useBar: true },
                        { id: 'loadsByRouteChart', useBar: true },
                        { id: 'loadsByRouteChart1', useBar: true },
                        { id: 'loadsByRouteChart2', useBar: true },
                    ];
                    chartConfigs.forEach((config) => {
                        const chartElement = document.getElementById(config.id);
                        if (chartElement) {
                            chartElement.dataset.initialized = 'false';
                        }
                    });
                    setTimeout(() => {
                        chartConfigs.forEach((config) => {
                            initChart(config.id, config.useBar);
                        });
                    }, 200);
                }
            }
        });
    });
}
if (typeof window !== 'undefined') {
    window.initDriversChart = initChart;
}
if (typeof window !== 'undefined') {
    window.initDriversChart = initChart;
}
